section {
    width: 100%;
    overflow: auto;
    padding: .8rem;

    header {
        margin-bottom: 1rem;
        display: flex;
        align-items: center;
        text-align: left;
        justify-content: space-between;

        .material-icons {
            padding: .9rem;

            background-color: brown;
            border-radius: 6px;
        }
    }

    .container {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        height: 100vh;
        overflow: auto;
        padding-bottom: 90px;

        .item {
            width: 95%;
            max-width: 390px;
            height: 395px;
            border-radius: 6px;
            background-color: rgb(7, 7, 7);
            margin: 2px;
            padding: .3rem;

            display: flex;
            align-items: left;
            justify-content: space-between;
            flex-direction: column;

            .groupFlex {
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: .3rem;

                .quantity {
                    width: 30px;
                    height: 30px;
                    border-radius: 50%;
                    background-color: brown;
                    text-align: center;
                }
            }

            p {
                padding: .3rem;
            }

            img {
                border-radius: 6px;
            }

            .textarea {
                width: 100%;
                height: 55px;
                border-radius: 6px;
                background-color: rgba(29, 29, 29, 0.137);
                padding: .3rem;
                color: grey;
                overflow: auto;
                text-align: left;
            }

            button {
                width: 100%;
                height: 85px;
                border-radius: 6px;
                border: none;
                outline: none;
                background-color: rgb(8, 8, 8);
                color: white;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0 .6rem;

                .material-icons {
                    font-size: 55px;
                    color: brown;
                    
                }
            }

            button:hover {
                cursor: pointer;
                
                .material-icons {
                    color: rgb(238, 65, 65);
                }
            }
        }
    }
}

@media (max-width: 600px) {
    header {
        text-align: center;
    }
    .container {
        display: flex !important;
        align-items: center;
        flex-direction: column;
        padding-bottom: 190px !important;
    }
}