#cartPage {
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: top;
    flex-direction: column;
    margin-top: 1.6rem;

    #textLeft {
        width: 90%;
        max-width: 520px;
        text-align: left;

        button {
            width: 55px;
            padding: .3rem;
            height: 25px;
            background-color: brown;
            color: white;
            border-radius: 8px 0;
            margin-bottom: .3rem;
            border: none;
            outline: none;
            cursor: pointer;
        }
    }

    #cart {
        width: 90%;
        max-width: 520px;
        height: 540px;
        background-color: rgb(24, 24, 24);
        border-radius: 6px 6px 0 0;
        padding: .8rem;

        #cartItems {
            width: 100%;
            height: 390px;
            margin-top: .9rem;
            overflow: auto;


            .cartItem {
                width: 100%;
                height: 60px;
                background-color: rgba(128, 128, 128, 0.432);
                margin-top: 2px;
                display: flex;
                align-items: center;
                justify-content: space-between;
                padding: 0 .6rem;
                

                p {
                    color: black;
                }

                .cartItemSettings {
                    display: flex;
                    align-items: center;

                    width: 90px;
                    justify-content: space-between;

                    button {
                        border: none;
                        outline: none;
                        width: 30px;
                        height: 30px;
                        border-radius: 50%;
                    }

                    .cartButtonAdd {
                        background-color: rgb(48, 202, 48);
                    }

                    .cartButtonTake {
                        background-color: brown;
                    }
                }
            }

        }
    }
    button {
        width: 90%;
        max-width: 520px;
        height: 45px;
        border: none;
        outline: none;
        border-radius: 0 0 4px 4px;
        background-color: brown;
        color: white;
        cursor: pointer;
        font-size: 1.1em;
    }
}

@media (max-width: 600px) {
    #cartPage>#cart {
        background-color: rgb(212, 212, 212);
    }
}