#DeliveryPage {
    width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    overflow: auto;

    #back {
        width: 100%;
        height: 100vh;
        padding: 1.4rem;
        padding-bottom: 3rem;
        overflow: auto;
        
        #textLeft {
            width: 80%;
            max-width: 520px;
            text-align: left;
            margin: auto;
    
            button {
                width: 55px;
                padding: .3rem;
                background-color: brown;
                color: white;
                border-radius: 8px 0;
                margin-bottom: .3rem;
                border: none;
                outline: none;
                cursor: pointer;
            }
        }
    
        .formDelivery {
            width: 80%;
            max-width: 520px;
            background-color: rgb(19, 19, 19);
            border-radius: 6px;
            padding: 1.3rem .9rem;
            margin: auto;
    
            h3 {
                margin-top: 1.2rem;
                margin-bottom: .3rem;
                color: grey;
            }


            h3:first-child {
                margin-top: 0;
            }

            input {
                width: 100%;
                height: 45px;
                background-color: rgb(37, 37, 37);
                padding-left: 3px;
                border-radius: 4px;
            }
    
            .group {
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: space-between;
                
                input {
                    width: 49%;
                }
            }
    
            .radios {
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: .3rem;
                .radioName {
                    width: 49%;
                    background-color: rgb(37, 37, 37);
                    display: flex;
                    align-items: center;
                    justify-content: left;
                    padding-left: .3rem;
                    border-radius: 4px;
                    gap: .7rem;
    
                    input {
                        width: 20px;
                    }
    
                    p {
                        color: grey;
                    }
    
                }
            }
    
            div#radio5 {
                width: 100%;
            }
    
            button {
                border: none;
                outline: none;
                height: 45px;
                width: 100%;
                border-radius: 4px;
                background-color: rgb(64, 148, 64);
                cursor: pointer;
    
                display: flex;
                align-items: center;
    
                img {
                    transform: translateX(10px);
                }
    
                h3 {
                    color: rgb(37, 37, 37);
                    width: 90%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    transform: translateY(-5px);
                }
            }
        }
    
        .formDelivery.max {
            height: auto;
        }
    }
}

@media (max-width: 600px) {
    #DeliveryPage {
        #textLeft {
            width: 95% !important;
        }
    }
    .formDelivery {
        padding: .7rem !important;
        width: 95% !important;
        background-color: rgb(202, 202, 202) !important;

        .group {
            flex-direction: column;

            input {
                width: 100% !important;
                margin-bottom: .3rem;
            }

        }

        .radios {
            .radioName {
                p {
                    color: grey;
                }
            }
        }
    
    }
}