menu {
    width: 90px;
    height: 100vh;
    padding: .8rem 0;
    background-color: brown;

    display: flex;
    align-items: center;
    flex-direction: column;

    .icons {
        margin: 1.3rem 0;

        background-color: rgba(19, 19, 19, 0.226);
        width: 40px;
        height: 40px;
        padding: 10px;
        border-radius: 50%;
    }

    .icons:hover {
        background-color: rgb(19, 19, 19);
    }
}

@media (max-width: 600px) {
    menu {
        height: 60px;
        width: 100vw;
        flex-direction: row;
        padding: 0 .8rem;
        position: fixed;
        bottom: 0;

        .icons {
            width: 40px;
            height: 40px;
            margin: 0 .9rem;
        }
    }
}